* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e5f5f2;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

ul {
  padding: 0;
  list-style: none;
}

.hide {
  display: none;
}

.search-form {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.wrapper {
  background-color: #2f55ee;
  padding: 40px 0;
}

.gallery {
  grid-gap: 32px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 24px auto;
  padding: 0;
  display: grid;
}

.info {
  text-align: center;
  justify-content: center;
  align-items: center;
  gap: 8px;
  margin: 0 16px;
  display: flex;
}

.photo-card {
  border-radius: 6px;
  transition: scale .25s ease-out;
  overflow: hidden;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .4);
}

.photo-card:hover {
  cursor: pointer;
  scale: 1.1;
}

.gallery img {
  width: 100%;
  object-fit: cover;
  display: block;
}

.search-form > button {
  color: #2f55ee;
  background-color: #f0f8ff;
  border: none;
}

button {
  width: 120px;
  cursor: pointer;
  border-radius: 4px;
  outline: none;
  padding: 6px 8px;
}

.button-wrapper {
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  padding-bottom: 40px;
  display: flex;
}

button.load-more {
  color: #414e52;
  width: 300px;
  height: 50px;
  background-color: #f4f5e9;
  border: none;
  border-radius: 10px;
  font-size: 25px;
  font-weight: 550;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, .4);
}

.search-form > input {
  width: 200px;
  color: #2f55ee;
  background-color: #f0f8ff;
  border-radius: 4px;
}

